var that;
export default {
    data() {
        return {
            tabCurrentIndex: 0,
            machine_config: '',
            machine_status: '',
            machine_id: '',
            navList: [
                {
                    state: 0,
                    text: '矿机配置',
                    orderList: []
                },
                {
                    state: 1,
                    text: '矿机状态',
                    orderList: []
                },
                {
                    state: 2,
                    text: '矿机记录',
                    orderList: []
                }
            ]
        };
    },
    onLoad(options) {
        that = this;
        this.machine_id = options.machine_id;
        this.tabCurrentIndex = 0; // 页面显示是默认选中第一个
        uni.request({
            url: this.url + 'usermachineinfo/' + '1/' + this.machine_id,
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success(res) {
                that.machine_config = res.data.data;
            }
        });
        uni.request({
            url: this.url + 'usermachineinfo/' + '2/' + this.machine_id,
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success(res) {
                that.machine_status = res.data.data;
            }
        });
    },
    methods: {
        changeTab(e) {
            this.tabCurrentIndex = e.target.current;
        },
        tabClick(index) {
            that.tabCurrentIndex = index;
        }
    }
};
